const exampleTransition = {
	onExit: ({ next, from: { route: { path } }, ...rest }) => {
		// console.info('onExit', path, rest)
		next()
	},

	onAfterExit: ({ from: { route: { path } }, ...rest }) => {
		// console.info('onAfterExit', path, rest)
	},

	onEnter: ({ next, to: { route: { path } }, ...rest }) => {
		// console.info('onEnter', path, rest)
		next()
	},

	onAfterEnter: ({ to: { route: { path } }, ...rest }) => {
		// console.info('onAfterEnter', path, rest)
	}
}

let prevHtml
let nextHtml
const paginationExample = {
	updateDom({ wrapper, newHtml, title, action, from, to }) {
		// if (!from.route.pagination && action === 'POP') {
		// 	log('TITS')
		// 	wrapper.innerHTML = ''
		// }

		if (to.route.pagination) {
			const { page: fromPageNumber } = from
			const { page: toPageNumber } = to

			log(toPageNumber)

			if (toPageNumber > fromPageNumber) {
				if (wrapper.querySelector(`[data-spon-page="${toPageNumber}"]`)) return

				newHtml.setAttribute('data-spon-page', toPageNumber)
				wrapper.appendChild(newHtml)
				prevHtml = newHtml
			} else {
				log('GO BACKWARD')
				if (prevHtml) {
					const tempPrev = prevHtml.previousElementSibling
					tempPrev && prevHtml.parentNode.removeChild(prevHtml)
					prevHtml = tempPrev
					nextHtml = null

					if (!tempPrev) {
						wrapper.innerHTML = ''
						newHtml.setAttribute('data-spon-page', toPageNumber)
						wrapper.appendChild(newHtml)
					}
				} else {
					if (!from.route.pagination) {
						wrapper.innerHTML = ''
					}

					newHtml.setAttribute('data-spon-page', toPageNumber)
					nextHtml = wrapper.querySelector(this.el)
					if (!nextHtml) {
						wrapper.appendChild(newHtml)
					} else {
						nextHtml = nextHtml.parentNode.insertBefore(newHtml, nextHtml)
					}
					prevHtml = null
					log('we have no prev content... so what cha wanna do')
					// debugger // eslint-disable-line
				}
			}
		} else {
			log('BACK FROM A NON PAGINATION PAGE')
			wrapper.innerHTML = ''
			wrapper.appendChild(newHtml)
		}

		document.title = title
	},

	onExit: ({ next }) => {
		next()
	},

	onEnter: ({ next }) => {
		next()
	}
}

export default [
	{
		path: '/',
		view: exampleTransition
	},
	{
		path: '/blog/',
		view: exampleTransition,
		children: {
			path: ':id',
			view: paginationExample,
			pagination: true
		}
	},
	{
		path: '/page-2/',
		view: exampleTransition
	},
	{
		path: '/page-3/',
		view: exampleTransition
	},
	{
		path: '/page-4/',
		view: exampleTransition
	},
	{
		path: '*',
		view: exampleTransition
	}
]
